// app/dashboard/page.tsx
"use client";

import React, { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import {
  ArrowPathIcon,
  ChartBarIcon,
  ClockIcon,
  DocumentMagnifyingGlassIcon,
  ExclamationTriangleIcon,
  DocumentTextIcon,
  DocumentArrowDownIcon,
  UserCircleIcon,
  MagnifyingGlassIcon,
  ArrowTrendingUpIcon,
  ExclamationCircleIcon,
  ChevronRightIcon,
  CheckCircleIcon,
  BellIcon,
  Cog6ToothIcon,
  HomeIcon,
  UsersIcon,
  CalendarIcon,
  EyeIcon,
  ChartPieIcon,
  ShieldExclamationIcon,
  DocumentChartBarIcon,
  ServerIcon,
  ArrowDownTrayIcon,
} from "@heroicons/react/24/outline";

// Import Chart.js
import {
  Chart as ChartJS,
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
} from 'chart.js';
import { Radar } from 'react-chartjs-2';

// Register ChartJS components
ChartJS.register(
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend
);

type ComparisonItem = {
  id: number;
  document_name: string;
  version_old_label: string;
  version_new_label: string;
  created_at: string;
  overall_risk_level?: "LOW" | "MEDIUM" | "HIGH" | string | null;
  changes_count?: number;
};

type Role = "ADMIN" | "MANAGER" | "VIEWER";

const API_BASE =
  process.env.NEXT_PUBLIC_API_BASE?.trim() || "http://127.0.0.1:8000";

// Modern Color Palette
const PRIMARY = "#3B82F6";
const PRIMARY_HOVER = "#2563EB";
const SECONDARY = "#10B981";
const SECONDARY_HOVER = "#059669";
const BACKGROUND = "#F8FAFC";
const CARD_BG = "#FFFFFF";
const BORDER = "#E2E8F0";
const TEXT_PRIMARY = "#1E293B";
const TEXT_SECONDARY = "#64748B";
const TEXT_MUTED = "#94A3B8";

function formatDateTime(iso: string) {
  try {
    return new Date(iso).toLocaleString("th-TH", {
      month: "short",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  } catch {
    return iso;
  }
}

function normalizeRisk(r?: string | null) {
  const x = (r || "").toUpperCase();
  if (x.includes("HIGH") || x.includes("‡∏™‡∏π‡∏á")) return "HIGH";
  if (x.includes("MEDIUM") || x.includes("‡∏Å‡∏•‡∏≤‡∏á")) return "MEDIUM";
  if (x.includes("LOW") || x.includes("‡∏ï‡πà‡∏≥")) return "LOW";
  return "LOW";
}

function RiskBadge({ level }: { level?: string | null }) {
  const r = normalizeRisk(level);
  const base = "inline-flex items-center gap-1.5 rounded-full px-3 py-1.5 text-xs font-semibold";
  
  if (r === "HIGH")
    return (
      <span className={`${base} bg-red-50 text-red-700 border border-red-200`}>
        <ShieldExclamationIcon className="h-3.5 w-3.5" />
        HIGH RISK
      </span>
    );
  if (r === "MEDIUM")
    return (
      <span className={`${base} bg-amber-50 text-amber-700 border border-amber-200`}>
        <ExclamationTriangleIcon className="h-3.5 w-3.5" />
        MEDIUM RISK
      </span>
    );
  return (
    <span className={`${base} bg-emerald-50 text-emerald-700 border border-emerald-200`}>
      <CheckCircleIcon className="h-3.5 w-3.5" />
      LOW RISK
    </span>
  );
}

function ModernCard({ children, className = "", hover = true }: { 
  children: React.ReactNode; 
  className?: string;
  hover?: boolean;
}) {
  return (
    <div className={`
      bg-white rounded-xl border border-slate-200 
      shadow-sm hover:shadow-md transition-shadow duration-300
      ${hover ? 'hover:border-slate-300' : ''}
      ${className}
    `}>
      {children}
    </div>
  );
}

function StatCard({ 
  title, 
  value, 
  icon, 
  trend, 
  subtitle,
  loading = false
}: { 
  title: string; 
  value: React.ReactNode; 
  icon: React.ReactNode;
  trend?: string;
  subtitle?: string;
  loading?: boolean;
}) {
  return (
    <ModernCard>
      <div className="p-5">
        <div className="flex items-start justify-between">
          <div>
            <div className="text-sm text-slate-500 font-medium mb-2">{title}</div>
            <div className="text-2xl font-bold text-slate-900">
              {loading ? (
                <div className="h-8 w-20 bg-slate-200 rounded animate-pulse"></div>
              ) : (
                value
              )}
            </div>
            {subtitle && (
              <div className="mt-1 text-sm text-slate-500">{subtitle}</div>
            )}
          </div>
          <div className="p-3 rounded-lg bg-gradient-to-br from-blue-50 to-blue-100 text-blue-600">
            {icon}
          </div>
        </div>
        {trend && (
          <div className="mt-4 pt-4 border-t border-slate-100">
            <div className="flex items-center text-sm">
              <ArrowTrendingUpIcon className="h-4 w-4 text-emerald-500 mr-1.5" />
              <span className="font-medium text-emerald-600">{trend}</span>
              <span className="text-slate-500 ml-1.5">from last month</span>
            </div>
          </div>
        )}
      </div>
    </ModernCard>
  );
}

function ActivityItem({ item }: { item: ComparisonItem }) {
  return (
    <div className="p-4 hover:bg-slate-50 rounded-lg transition-all duration-200 group">
      <div className="flex items-start gap-4">
        <div className="w-10 h-10 rounded-lg bg-gradient-to-br from-blue-100 to-blue-50 flex items-center justify-center flex-shrink-0">
          <DocumentTextIcon className="h-5 w-5 text-blue-600" />
        </div>
        <div className="flex-1 min-w-0">
          <div className="flex items-center gap-3 mb-2">
            <h4 className="font-semibold text-slate-900 truncate">{item.document_name}</h4>
            <RiskBadge level={item.overall_risk_level} />
          </div>
          <p className="text-sm text-slate-600 mb-2">
            {item.version_old_label} ‚Üí {item.version_new_label}
          </p>
          <div className="flex items-center gap-4 text-xs text-slate-500">
            <span>{formatDateTime(item.created_at)}</span>
            <span>‚Ä¢</span>
            <span>{item.changes_count || 0} changes</span>
          </div>
        </div>
        <Link 
          href={`/compare/${item.id}`}
          className="opacity-0 group-hover:opacity-100 transition-opacity duration-200 text-blue-600 hover:text-blue-700"
        >
          <ChevronRightIcon className="h-5 w-5" />
        </Link>
      </div>
    </div>
  );
}

// SpiderGraph Component
function SpiderGraph({ 
  data, 
  title,
  loading = false
}: { 
  data: {
    labels: string[];
    values: number[];
  };
  title: string;
  loading?: boolean;
}) {
  if (loading) {
    return (
      <div className="h-64 flex items-center justify-center">
        <ArrowPathIcon className="h-8 w-8 animate-spin text-slate-400" />
      </div>
    );
  }

  const chartData = {
    labels: data.labels,
    datasets: [
      {
        label: 'Current Risk Score',
        data: data.values,
        backgroundColor: 'rgba(59, 130, 246, 0.2)',
        borderColor: 'rgb(59, 130, 246)',
        borderWidth: 2,
        pointBackgroundColor: 'rgb(59, 130, 246)',
        pointBorderColor: '#fff',
        pointBorderWidth: 2,
        pointRadius: 6,
      },
      {
        label: 'Baseline',
        data: data.labels.map(() => 5), // Baseline at 5
        backgroundColor: 'rgba(148, 163, 184, 0.1)',
        borderColor: 'rgb(148, 163, 184)',
        borderWidth: 1,
        borderDash: [5, 5],
        pointRadius: 0,
      }
    ],
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    scales: {
      r: {
        angleLines: {
          display: true,
          color: 'rgba(203, 213, 225, 0.3)',
        },
        grid: {
          color: 'rgba(203, 213, 225, 0.3)',
        },
        pointLabels: {
          color: '#64748B',
          font: {
            size: 11,
            weight: '600' as const,
          },
        },
        ticks: {
          display: false,
          backdropColor: 'transparent',
        },
        suggestedMin: 0,
        suggestedMax: 10,
      },
    },
    plugins: {
      legend: {
        display: false,
      },
      tooltip: {
        backgroundColor: 'rgba(30, 41, 59, 0.9)',
        titleColor: '#F1F5F9',
        bodyColor: '#F1F5F9',
        borderColor: '#475569',
        borderWidth: 1,
        cornerRadius: 8,
        padding: 12,
        displayColors: false,
        callbacks: {
          label: function(context: any) {
            return `${context.label}: ${context.raw}/10`;
          }
        }
      },
    },
  };

  return (
    <div className="h-64">
      <Radar data={chartData} options={options} />
    </div>
  );
}

function getStoredRole(): Role {
  if (typeof window === "undefined") return "MANAGER";
  const v = (localStorage.getItem("dv_role") || "MANAGER").toUpperCase();
  if (v === "ADMIN" || v === "MANAGER" || v === "VIEWER")
    return v;
  return "MANAGER";
}

function setStoredRole(role: Role) {
  if (typeof window === "undefined") return;
  localStorage.setItem("dv_role", role);
}

// Button Component ‡∏ó‡∏µ‡πà‡πÑ‡∏°‡πà‡πÉ‡∏ä‡πâ asChild
function ModernButton({ 
  children, 
  variant = "primary",
  fullWidth = false,
  className = "",
  loading = false,
  href,
  ...props 
}: React.ButtonHTMLAttributes<HTMLButtonElement> & {
  variant?: "primary" | "secondary" | "outline" | "ghost";
  fullWidth?: boolean;
  loading?: boolean;
  href?: string;
}) {
  const baseClasses = "rounded-lg font-semibold transition-all duration-200 flex items-center justify-center gap-2";
  const widthClass = fullWidth ? "w-full" : "";
  
  const variants = {
    primary: `bg-gradient-to-r from-blue-600 to-blue-700 text-white hover:from-blue-700 hover:to-blue-800 active:scale-[0.98] shadow-sm hover:shadow ${widthClass}`,
    secondary: `bg-gradient-to-r from-emerald-600 to-emerald-700 text-white hover:from-emerald-700 hover:to-emerald-800 active:scale-[0.98] shadow-sm hover:shadow ${widthClass}`,
    outline: `bg-white text-slate-700 border border-slate-300 hover:bg-slate-50 hover:border-slate-400 ${widthClass}`,
    ghost: `bg-transparent text-slate-600 hover:bg-slate-100 hover:text-slate-900 ${widthClass}`
  };

  // ‡∏ñ‡πâ‡∏≤‡∏°‡∏µ href ‡πÉ‡∏´‡πâ‡πÉ‡∏ä‡πâ Link
  if (href) {
    return (
      <Link
        href={href}
        className={`${baseClasses} ${variants[variant]} ${className} px-4 py-3 text-sm`}
      >
        {loading && <ArrowPathIcon className="h-4 w-4 animate-spin" />}
        {children}
      </Link>
    );
  }

  // ‡∏õ‡∏∏‡πà‡∏°‡∏õ‡∏Å‡∏ï‡∏¥
  return (
    <button
      className={`${baseClasses} ${variants[variant]} ${className} px-4 py-3 text-sm disabled:opacity-50 disabled:cursor-not-allowed`}
      disabled={loading || props.disabled}
      {...props}
    >
      {loading && <ArrowPathIcon className="h-4 w-4 animate-spin" />}
      {children}
    </button>
  );
}

export default function DashboardPage() {
  const [items, setItems] = useState<ComparisonItem[]>([]);
  const [loading, setLoading] = useState(true);
  const [refreshing, setRefreshing] = useState(false);
  const [pinging, setPinging] = useState(false);
  const [apiOk, setApiOk] = useState<boolean | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [role, setRole] = useState<Role>("MANAGER");

  useEffect(() => {
    setRole(getStoredRole());
    fetchData();
  }, []);

  useEffect(() => {
    setStoredRole(role);
  }, [role]);

  const fetchData = async (isRefresh = false) => {
    if (isRefresh) {
      setRefreshing(true);
    } else {
      setLoading(true);
    }
    setError(null);
    
    try {
      const res = await fetch(`${API_BASE}/comparisons?limit=100`, {
        cache: "no-store",
      });
      if (!res.ok) throw new Error(`Failed to load data (${res.status})`);
      const data: ComparisonItem[] = await res.json();
      setItems(Array.isArray(data) ? data : []);
      setApiOk(true);
    } catch (e: any) {
      setError(e?.message || "Failed to load data");
      setApiOk(false);
      setItems([]);
    } finally {
      setLoading(false);
      setRefreshing(false);
    }
  };

  const pingApi = async () => {
    setPinging(true);
    try {
      const res = await fetch(`${API_BASE}/health`, { cache: "no-store" });
      setApiOk(res.ok);
    } catch {
      setApiOk(false);
    } finally {
      setPinging(false);
    }
  };

  const computed = useMemo(() => {
    const list = [...items].sort(
      (a, b) =>
        new Date(b.created_at).getTime() - new Date(a.created_at).getTime()
    );

    const comparisons = list.length;
    const docsSet = new Set(list.map((x) => x.document_name).filter(Boolean));
    const documents = docsSet.size;

    let high = 0, med = 0, low = 0;
    list.forEach((x) => {
      const r = normalizeRisk(x.overall_risk_level);
      if (r === "HIGH") high++;
      else if (r === "MEDIUM") med++;
      else low++;
    });

    // 30-day trend
    const days = 30;
    const now = new Date();
    const buckets = new Array(days).fill(0);
    list.forEach((x) => {
      const d = new Date(x.created_at);
      const diff = Math.floor(
        (now.getTime() - d.getTime()) / (1000 * 60 * 60 * 24)
      );
      if (diff >= 0 && diff < days) {
        const idx = days - 1 - diff;
        buckets[idx] += 1;
      }
    });

    const latest = list.slice(0, 6);
    const totalChanges = list.reduce((sum, item) => sum + (item.changes_count || 0), 0);
    const avgChanges = comparisons > 0 ? Math.round(totalChanges / comparisons) : 0;

    // Recent activity (last 7 days)
    const recentActivity = list.filter((x) => {
      const d = new Date(x.created_at);
      const diff = Math.floor((now.getTime() - d.getTime()) / (1000 * 60 * 60 * 24));
      return diff < 7;
    }).length;

    // ‡∏Ñ‡∏≥‡∏ô‡∏ß‡∏ì spider graph data
    const spiderData = {
      labels: [
        'Confidentiality',
        'Integrity', 
        'Compliance',
        'Financial',
        'Operational',
        'Reputational'
      ],
      values: [
        // ‡∏Ñ‡∏≥‡∏ô‡∏ß‡∏ì‡∏à‡∏≤‡∏Å risk level ‡πÅ‡∏•‡∏∞ changes count
        Math.min(10, (high * 3) + (med * 2) + low),
        Math.min(10, Math.floor(totalChanges / Math.max(1, comparisons) * 2)),
        Math.min(10, (high + med) * 2),
        Math.min(10, high * 4),
        Math.min(10, med * 3),
        Math.min(10, (high * 2) + med)
      ]
    };

    return {
      list,
      comparisons,
      documents,
      high,
      med,
      low,
      trend: buckets,
      latest,
      totalChanges,
      avgChanges,
      recentActivity,
      riskPercentage: comparisons > 0 ? ((high + med) / comparisons * 100).toFixed(1) : "0",
      spiderData,
    };
  }, [items]);

  const canSeeRisk = role !== "VIEWER";
  const canDownload = role === "ADMIN" || role === "MANAGER";

  const sidebarItems = [
    { id: "dashboard", icon: <HomeIcon className="h-5 w-5" />, label: "Dashboard", active: true, href: "/dashboard" },
    { id: "compare", icon: <DocumentMagnifyingGlassIcon className="h-5 w-5" />, label: "Compare", badge: computed.comparisons, href: "/" },
    { id: "history", icon: <ClockIcon className="h-5 w-5" />, label: "History", href: "/history" },
    { id: "reports", icon: <DocumentChartBarIcon className="h-5 w-5" />, label: "Reports", href: "/reports/generate" },
    { id: "analytics", icon: <ChartPieIcon className="h-5 w-5" />, label: "Analytics", href: "#risk-analysis" },
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50/30">
      {/* Header */}
      <header className="sticky top-0 z-50 bg-white/80 backdrop-blur-xl border-b border-slate-200">
        <div className="max-w-7xl mx-auto px-4">
          <div className="flex items-center justify-between h-16">
            {/* Logo */}
            <div className="flex items-center gap-3">
              <Link href="/" className="flex items-center gap-2.5 group">
                <div className="w-9 h-9 rounded-xl bg-gradient-to-br from-blue-600 to-blue-700 flex items-center justify-center shadow-sm">
                  <DocumentMagnifyingGlassIcon className="h-5 w-5 text-white" />
                </div>
                <div>
                  <div className="text-lg font-bold text-slate-900">DocAnalyzer</div>
                  <div className="text-xs text-slate-500">AI Document Analysis</div>
                </div>
              </Link>
              
              <div className="hidden md:flex items-center ml-6">
                <div className="relative">
                  <MagnifyingGlassIcon className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-slate-400" />
                  <input
                    type="search"
                    placeholder="Search documents..."
                    className="pl-10 pr-4 py-2 bg-slate-100 rounded-lg text-sm w-64 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:bg-white transition-all duration-200"
                  />
                </div>
              </div>
            </div>

            {/* Right Actions */}
            <div className="flex items-center gap-3">
              {/* Status */}
              <div className="hidden sm:flex items-center gap-2 px-3 py-2 bg-slate-100 rounded-lg">
                <div className={`h-2 w-2 rounded-full ${apiOk ? 'bg-emerald-500' : apiOk === false ? 'bg-red-500' : 'bg-slate-400'}`} />
                <span className="text-sm text-slate-600">
                  {pinging ? 'Checking...' : apiOk ? 'API Online' : apiOk === false ? 'API Offline' : 'Unknown'}
                </span>
              </div>

              {/* Role Selector */}
              <div className="relative group">
                <button className="flex items-center gap-2 px-3 py-2 bg-blue-50 text-blue-700 rounded-lg hover:bg-blue-100 transition-colors text-sm font-semibold">
                  <UserCircleIcon className="h-4 w-4" />
                  {role}
                  <ChevronRightIcon className="h-3 w-3 transform group-hover:rotate-90 transition-transform" />
                </button>
                <div className="absolute right-0 top-full mt-1 w-32 bg-white rounded-lg shadow-lg border border-slate-200 opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 z-50">
                  {["ADMIN", "MANAGER", "VIEWER"].map((r) => (
                    <button
                      key={r}
                      onClick={() => setRole(r as Role)}
                      className={`w-full text-left px-4 py-2 text-sm hover:bg-slate-50 transition-colors first:rounded-t-lg last:rounded-b-lg ${
                        role === r ? 'text-blue-600 font-semibold' : 'text-slate-700'
                      }`}
                    >
                      {r}
                    </button>
                  ))}
                </div>
              </div>

              {/* Action Buttons */}
              <button
                onClick={() => fetchData(true)}
                disabled={refreshing}
                className="p-2 text-slate-600 hover:text-slate-900 hover:bg-slate-100 rounded-lg transition-colors disabled:opacity-50"
                title="Refresh"
              >
                <ArrowPathIcon className={`h-5 w-5 ${refreshing ? 'animate-spin' : ''}`} />
              </button>

              <button className="p-2 relative">
                <BellIcon className="h-5 w-5 text-slate-600 hover:text-slate-900 transition-colors" />
                {computed.high > 0 && (
                  <span className="absolute -top-1 -right-1 h-4 w-4 bg-red-500 text-white text-xs rounded-full flex items-center justify-center font-bold">
                    {Math.min(computed.high, 9)}
                  </span>
                )}
              </button>

              <button className="p-2">
                <Cog6ToothIcon className="h-5 w-5 text-slate-600 hover:text-slate-900 transition-colors" />
              </button>
            </div>
          </div>
        </div>
      </header>

      <div className="max-w-7xl mx-auto px-4 py-6">
        <div className="flex flex-col lg:flex-row gap-6">
          {/* Sidebar */}
          <aside className="lg:w-64 flex-shrink-0">
            <ModernCard hover={false} className="sticky top-24">
              <div className="p-4">
                {/* User Profile */}
                <div className="mb-6 p-3 bg-gradient-to-r from-blue-50 to-blue-100/50 rounded-lg">
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 rounded-lg bg-gradient-to-br from-blue-600 to-blue-700 flex items-center justify-center text-white font-semibold">
                      {role.charAt(0)}
                    </div>
                    <div>
                      <div className="font-semibold text-slate-900">{role} User</div>
                      <div className="text-xs text-slate-500">Active now</div>
                    </div>
                  </div>
                </div>

                {/* Navigation */}
                <div className="space-y-1">
                  {sidebarItems.map((item) => (
                    <Link
                      key={item.id}
                      href={item.href}
                      className={`
                        flex items-center justify-between gap-3 px-3 py-2.5 rounded-lg
                        transition-all duration-200 group
                        ${item.active 
                          ? 'bg-blue-50 text-blue-700 font-semibold' 
                          : 'text-slate-700 hover:bg-slate-50 hover:text-slate-900'
                        }
                      `}
                    >
                      <div className="flex items-center gap-3">
                        <div className={item.active ? 'text-blue-600' : 'text-slate-500 group-hover:text-slate-700'}>
                          {item.icon}
                        </div>
                        <span className="text-sm">{item.label}</span>
                      </div>
                      {item.badge && (
                        <span className="bg-blue-100 text-blue-700 text-xs font-semibold px-2 py-0.5 rounded-full">
                          {item.badge}
                        </span>
                      )}
                    </Link>
                  ))}
                </div>

                {/* Quick Actions */}
                <div className="mt-8">
                  <div className="text-xs font-semibold text-slate-500 uppercase tracking-wider mb-3 px-3">
                    Quick Actions
                  </div>
                  <div className="space-y-2">
                    <Link
                      href="/"
                      className="block w-full bg-gradient-to-r from-blue-600 to-blue-700 text-white hover:from-blue-700 hover:to-blue-800 active:scale-[0.98] shadow-sm hover:shadow rounded-lg font-semibold transition-all duration-200 flex items-center justify-center gap-2 px-4 py-3 text-sm"
                    >
                      <DocumentMagnifyingGlassIcon className="h-4 w-4" />
                      New Comparison
                    </Link>
                    
                    <Link
                      href="/reports/generate"
                      className="block w-full bg-white text-slate-700 border border-slate-300 hover:bg-slate-50 hover:border-slate-400 rounded-lg font-semibold transition-all duration-200 flex items-center justify-center gap-2 px-4 py-3 text-sm"
                    >
                      <DocumentArrowDownIcon className="h-4 w-4" />
                      Generate Report
                    </Link>
                  </div>
                </div>
              </div>
            </ModernCard>
          </aside>

          {/* Main Content */}
          <main className="flex-1 min-w-0">
            {/* Welcome Section */}
            <div className="mb-6">
              <ModernCard className="bg-gradient-to-r from-blue-600 to-blue-700">
                <div className="p-6">
                  <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
                    <div>
                      <h1 className="text-2xl font-bold text-white mb-2">Welcome back! üëã</h1>
                      <p className="text-blue-100">
                        You have {computed.recentActivity} new comparisons this week
                      </p>
                    </div>
                    <div className="flex flex-wrap gap-2">
                      <Link
                        href="/"
                        className="bg-gradient-to-r from-emerald-600 to-emerald-700 text-white hover:from-emerald-700 hover:to-emerald-800 active:scale-[0.98] shadow-sm hover:shadow rounded-lg font-semibold transition-all duration-200 flex items-center justify-center gap-2 px-4 py-3 text-sm"
                      >
                        <DocumentMagnifyingGlassIcon className="h-4 w-4" />
                        Start New Analysis
                      </Link>
                      
                      <button className="bg-transparent text-white border border-white/30 hover:bg-white/10 rounded-lg font-semibold transition-all duration-200 flex items-center justify-center gap-2 px-4 py-3 text-sm">
                        View Insights
                      </button>
                    </div>
                  </div>
                  <div className="mt-6 grid grid-cols-2 md:grid-cols-4 gap-4">
                    <div className="bg-white/10 rounded-lg p-3 backdrop-blur-sm">
                      <div className="text-blue-100 text-sm font-medium">Active</div>
                      <div className="text-white text-xl font-bold">{computed.documents}</div>
                    </div>
                    <div className="bg-white/10 rounded-lg p-3 backdrop-blur-sm">
                      <div className="text-blue-100 text-sm font-medium">Total</div>
                      <div className="text-white text-xl font-bold">{computed.comparisons}</div>
                    </div>
                    <div className="bg-white/10 rounded-lg p-3 backdrop-blur-sm">
                      <div className="text-blue-100 text-sm font-medium">Changes</div>
                      <div className="text-white text-xl font-bold">{computed.totalChanges}</div>
                    </div>
                    <div className="bg-white/10 rounded-lg p-3 backdrop-blur-sm">
                      <div className="text-blue-100 text-sm font-medium">Avg/File</div>
                      <div className="text-white text-xl font-bold">{computed.avgChanges}</div>
                    </div>
                  </div>
                </div>
              </ModernCard>
            </div>

            {/* Error Alert */}
            {error && (
              <div className="mb-6">
                <ModernCard className="border-red-200 bg-red-50">
                  <div className="p-4">
                    <div className="flex items-start gap-3">
                      <ExclamationCircleIcon className="h-5 w-5 text-red-500 flex-shrink-0 mt-0.5" />
                      <div className="flex-1">
                        <div className="font-semibold text-red-800">Failed to load data</div>
                        <div className="text-sm text-red-600 mt-1">{error}</div>
                      </div>
                      <button
                        onClick={() => fetchData(true)}
                        className="text-sm font-medium text-red-600 hover:text-red-800"
                      >
                        Retry
                      </button>
                    </div>
                  </div>
                </ModernCard>
              </div>
            )}

            {/* Stats Grid */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-6">
              <StatCard
                title="Active Documents"
                value={computed.documents}
                icon={<DocumentTextIcon className="h-6 w-6" />}
                trend="+12.5%"
                subtitle="Currently monitored"
                loading={loading}
              />
              <StatCard
                title="Total Comparisons"
                value={computed.comparisons}
                icon={<ChartBarIcon className="h-6 w-6" />}
                trend="+8.2%"
                subtitle="All time"
                loading={loading}
              />
              <StatCard
                title="High Risk Items"
                value={computed.high}
                icon={<ExclamationTriangleIcon className="h-6 w-6" />}
                trend="-3.1%"
                subtitle="Requires attention"
                loading={loading}
              />
              <StatCard
                title="Avg Processing Time"
                value="<2s"
                icon={<ClockIcon className="h-6 w-6" />}
                trend="+15%"
                subtitle="System performance"
                loading={loading}
              />
            </div>

            {/* Two Column Layout */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              {/* Recent Activity */}
              <ModernCard>
                <div className="p-5">
                  <div className="flex items-center justify-between mb-4">
                    <div>
                      <h2 className="text-lg font-semibold text-slate-900">Recent Activity</h2>
                      <p className="text-sm text-slate-500 mt-1">Latest document comparisons</p>
                    </div>
                    <Link 
                      href="/history" 
                      className="text-sm font-medium text-blue-600 hover:text-blue-700"
                    >
                      View All
                    </Link>
                  </div>
                  
                  <div className="space-y-1">
                    {loading ? (
                      <div className="py-8 text-center">
                        <ArrowPathIcon className="h-8 w-8 animate-spin mx-auto text-slate-400" />
                        <p className="mt-3 text-sm text-slate-500">Loading activities...</p>
                      </div>
                    ) : computed.latest.length === 0 ? (
                      <div className="py-8 text-center">
                        <div className="text-slate-500">No recent activity</div>
                        <Link href="/" className="mt-3 text-sm font-medium text-blue-600 hover:text-blue-700 inline-block">
                          Start your first comparison ‚Üí
                        </Link>
                      </div>
                    ) : (
                      computed.latest.map((item) => (
                        <ActivityItem key={item.id} item={item} />
                      ))
                    )}
                  </div>
                </div>
              </ModernCard>

              {/* Right Column */}
              <div className="space-y-6">
                {/* Risk Distribution */}
                <ModernCard>
                  <div className="p-5">
                    <h2 className="text-lg font-semibold text-slate-900 mb-4">Risk Distribution</h2>
                    {canSeeRisk ? (
                      <div className="space-y-5">
                        <div className="space-y-4">
                          {/* High Risk */}
                          <div>
                            <div className="flex justify-between text-sm mb-2">
                              <span className="font-medium text-red-700">High Risk</span>
                              <span className="font-semibold text-slate-900">{computed.high}</span>
                            </div>
                            <div className="h-2 bg-red-100 rounded-full overflow-hidden">
                              <div 
                                className="h-full bg-gradient-to-r from-red-500 to-red-600 rounded-full transition-all duration-500" 
                                style={{ width: `${(computed.high / Math.max(1, computed.comparisons)) * 100}%` }}
                              />
                            </div>
                          </div>
                          {/* Medium Risk */}
                          <div>
                            <div className="flex justify-between text-sm mb-2">
                              <span className="font-medium text-amber-700">Medium Risk</span>
                              <span className="font-semibold text-slate-900">{computed.med}</span>
                            </div>
                            <div className="h-2 bg-amber-100 rounded-full overflow-hidden">
                              <div 
                                className="h-full bg-gradient-to-r from-amber-500 to-amber-600 rounded-full transition-all duration-500" 
                                style={{ width: `${(computed.med / Math.max(1, computed.comparisons)) * 100}%` }}
                              />
                            </div>
                          </div>
                          {/* Low Risk */}
                          <div>
                            <div className="flex justify-between text-sm mb-2">
                              <span className="font-medium text-emerald-700">Low Risk</span>
                              <span className="font-semibold text-slate-900">{computed.low}</span>
                            </div>
                            <div className="h-2 bg-emerald-100 rounded-full overflow-hidden">
                              <div 
                                className="h-full bg-gradient-to-r from-emerald-500 to-emerald-600 rounded-full transition-all duration-500" 
                                style={{ width: `${(computed.low / Math.max(1, computed.comparisons)) * 100}%` }}
                              />
                            </div>
                          </div>
                        </div>
                        
                        <div className="pt-4 border-t border-slate-100">
                          <div className="flex items-center justify-between">
                            <div className="text-sm text-slate-600">
                              <span className="font-semibold text-slate-900">{computed.riskPercentage}%</span> require attention
                            </div>
                            <div className="text-xs px-2 py-1 bg-slate-100 text-slate-600 rounded">
                              Total: {computed.comparisons}
                            </div>
                          </div>
                        </div>
                      </div>
                    ) : (
                      <div className="text-center py-8">
                        <ShieldExclamationIcon className="h-12 w-12 text-slate-300 mx-auto mb-3" />
                        <div className="text-slate-500">Risk data is not available for your role</div>
                      </div>
                    )}
                  </div>
                </ModernCard>

                {/* Spider Graph - Risk Analysis */}
                <ModernCard id="risk-analysis">    
                  <div className="p-5">
                    <div className="flex items-center justify-between mb-4">
                      <div>
                        <h2 className="text-lg font-semibold text-slate-900">Risk Analysis</h2>
                        <p className="text-sm text-slate-500 mt-1">Multi-dimensional risk assessment</p>
                      </div>
                      <div className="flex items-center gap-2">
                        <div className="flex items-center gap-1">
                          <div className="h-2 w-2 rounded-full bg-blue-500"></div>
                          <span className="text-xs text-slate-600">Current</span>
                        </div>
                        <div className="flex items-center gap-1">
                          <div className="h-2 w-2 rounded-full border border-slate-400"></div>
                          <span className="text-xs text-slate-600">Baseline</span>
                        </div>
                      </div>
                    </div>
                    
                    <SpiderGraph 
                      data={computed.spiderData}
                      title="Risk Assessment"
                      loading={loading}
                    />
                    
                    <div className="mt-4 grid grid-cols-2 gap-3">
                      <div className="text-center p-2 bg-blue-50 rounded-lg">
                        <div className="text-2xl font-bold text-blue-600">
                          {((computed.spiderData.values.reduce((a, b) => a + b, 0) / 60) * 100).toFixed(1)}%
                        </div>
                        <div className="text-xs text-blue-700 font-medium">Overall Risk Score</div>
                      </div>
                      <div className="text-center p-2 bg-slate-50 rounded-lg">
                        <div className="text-2xl font-bold text-slate-600">
                          {computed.spiderData.labels[
                            computed.spiderData.values.findIndex(v => v === Math.max(...computed.spiderData.values))
                          ]}
                        </div>
                        <div className="text-xs text-slate-700 font-medium">Highest Risk Area</div>
                      </div>
                    </div>
                  </div>
                </ModernCard>
              </div>
            </div>

            {/* Recent Documents */}
            <div className="mt-6">
              <ModernCard>
                <div className="p-5">
                  <div className="flex items-center justify-between mb-4">
                    <div>
                      <h2 className="text-lg font-semibold text-slate-900">Recent Documents</h2>
                      <p className="text-sm text-slate-500 mt-1">Recently analyzed documents</p>
                    </div>
                    <Link 
                      href="/history" 
                      className="text-sm font-medium text-blue-600 hover:text-blue-700"
                    >
                      View All
                    </Link>
                  </div>
                  
                  <div className="overflow-x-auto rounded-lg border border-slate-200">
                    <table className="w-full">
                      <thead>
                        <tr className="bg-slate-50 border-b border-slate-200">
                          <th className="text-left p-3 text-sm font-semibold text-slate-700">Document</th>
                          <th className="text-left p-3 text-sm font-semibold text-slate-700">Versions</th>
                          <th className="text-left p-3 text-sm font-semibold text-slate-700">Last Compared</th>
                          <th className="text-left p-3 text-sm font-semibold text-slate-700">Risk Level</th>
                          <th className="text-left p-3 text-sm font-semibold text-slate-700">Actions</th>
                        </tr>
                      </thead>
                      <tbody>
                        {loading ? (
                          <tr>
                            <td colSpan={5} className="p-8 text-center">
                              <ArrowPathIcon className="h-8 w-8 animate-spin mx-auto text-slate-400" />
                              <p className="mt-3 text-sm text-slate-500">Loading documents...</p>
                            </td>
                          </tr>
                        ) : computed.list.length === 0 ? (
                          <tr>
                            <td colSpan={5} className="p-8 text-center">
                              <div className="text-slate-500">No documents yet</div>
                              <Link href="/" className="mt-3 text-sm font-medium text-blue-600 hover:text-blue-700 inline-block">
                                Start your first comparison ‚Üí
                              </Link>
                            </td>
                          </tr>
                        ) : (
                          computed.list.slice(0, 5).map((item) => (
                            <tr key={item.id} className="border-b border-slate-100 last:border-0 hover:bg-slate-50 transition-colors">
                              <td className="p-3">
                                <div className="font-medium text-slate-900">{item.document_name}</div>
                              </td>
                              <td className="p-3">
                                <div className="text-sm text-slate-600">
                                  <span className="font-medium">{item.version_old_label}</span> ‚Üí 
                                  <span className="font-medium">{item.version_new_label}</span>
                                </div>
                              </td>
                              <td className="p-3">
                                <div className="text-sm text-slate-600">{formatDateTime(item.created_at)}</div>
                              </td>
                              <td className="p-3">
                                <RiskBadge level={item.overall_risk_level} />
                              </td>
                              <td className="p-3">
                                <div className="flex items-center gap-2">
                                  <Link
                                    href={`/compare/${item.id}`}
                                    className="p-1.5 text-blue-600 hover:text-blue-700 hover:bg-blue-50 rounded transition-colors"
                                    title="View Details"
                                  >
                                    <EyeIcon className="h-4 w-4" />
                                  </Link>
                                  {canDownload && (
                                    <button
                                      className="p-1.5 text-slate-600 hover:text-slate-900 hover:bg-slate-100 rounded transition-colors"
                                      title="Download Report"
                                    >
                                      <ArrowDownTrayIcon className="h-4 w-4" />
                                    </button>
                                  )}
                                </div>
                              </td>
                            </tr>
                          ))
                        )}
                      </tbody>
                    </table>
                  </div>
                </div>
              </ModernCard>
            </div>

            {/* Advanced Risk Analysis */}
            <div className="mt-6">
              <ModernCard>
                <div className="p-6">
                  <div className="flex items-center justify-between mb-6">
                    <div>
                      <h2 className="text-xl font-semibold text-slate-900">Advanced Risk Analysis</h2>
                      <p className="text-sm text-slate-500 mt-1">Comprehensive multi-dimensional risk assessment across key domains</p>
                    </div>
                    <div className="flex items-center gap-4">
                      <div className="flex items-center gap-2">
                        <div className="h-3 w-3 rounded-full bg-blue-500"></div>
                        <span className="text-sm text-slate-600">Current Risk</span>
                      </div>
                      <div className="flex items-center gap-2">
                        <div className="h-3 w-3 rounded-full border-2 border-slate-400"></div>
                        <span className="text-sm text-slate-600">Baseline</span>
                      </div>
                    </div>
                  </div>
                  
                  <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                    <div className="lg:col-span-2">
                      <SpiderGraph 
                        data={computed.spiderData}
                        title="Risk Assessment Radar"
                        loading={loading}
                      />
                    </div>
                    
                    <div className="space-y-4">
                      <div>
                        <div className="flex justify-between mb-1">
                          <span className="text-sm font-medium text-slate-700">Confidentiality</span>
                          <span className="text-sm font-bold text-slate-900">{computed.spiderData.values[0]}/10</span>
                        </div>
                        <div className="h-2 bg-slate-200 rounded-full overflow-hidden">
                          <div 
                            className="h-full bg-gradient-to-r from-blue-500 to-blue-600 rounded-full"
                            style={{ width: `${computed.spiderData.values[0] * 10}%` }}
                          />
                        </div>
                      </div>
                      
                      <div>
                        <div className="flex justify-between mb-1">
                          <span className="text-sm font-medium text-slate-700">Integrity</span>
                          <span className="text-sm font-bold text-slate-900">{computed.spiderData.values[1]}/10</span>
                        </div>
                        <div className="h-2 bg-slate-200 rounded-full overflow-hidden">
                          <div 
                            className="h-full bg-gradient-to-r from-emerald-500 to-emerald-600 rounded-full"
                            style={{ width: `${computed.spiderData.values[1] * 10}%` }}
                          />
                        </div>
                      </div>
                      
                      <div>
                        <div className="flex justify-between mb-1">
                          <span className="text-sm font-medium text-slate-700">Compliance</span>
                          <span className="text-sm font-bold text-slate-900">{computed.spiderData.values[2]}/10</span>
                        </div>
                        <div className="h-2 bg-slate-200 rounded-full overflow-hidden">
                          <div 
                            className="h-full bg-gradient-to-r from-amber-500 to-amber-600 rounded-full"
                            style={{ width: `${computed.spiderData.values[2] * 10}%` }}
                          />
                        </div>
                      </div>
                      
                      <div className="bg-gradient-to-r from-slate-50 to-slate-100 rounded-lg p-4">
                        <div className="text-sm font-semibold text-slate-900 mb-2">Risk Insights</div>
                        <ul className="text-xs text-slate-600 space-y-1">
                          {computed.spiderData.values[0] > 7 && (
                            <li className="flex items-center gap-2">
                              <ExclamationTriangleIcon className="h-3 w-3 text-red-500" />
                              High confidentiality risk detected
                            </li>
                          )}
                          {computed.spiderData.values[3] > 6 && (
                            <li className="flex items-center gap-2">
                              <ExclamationTriangleIcon className="h-3 w-3 text-amber-500" />
                              Financial impact considerations
                            </li>
                          )}
                          {computed.high > 0 && (
                            <li className="flex items-center gap-2">
                              <ShieldExclamationIcon className="h-3 w-3 text-red-500" />
                              {computed.high} high-risk items need attention
                            </li>
                          )}
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              </ModernCard>
            </div>

            {/* API Status & Footer */}
            <div className="mt-6">
              <ModernCard>
                <div className="p-5">
                  <div className="flex flex-col sm:flex-row items-center justify-between gap-4">
                    <div className="flex items-center gap-3">
                      <div className="p-2 rounded-lg bg-gradient-to-br from-slate-100 to-slate-200">
                        <ServerIcon className="h-5 w-5 text-slate-700" />
                      </div>
                      <div>
                        <div className="font-semibold text-slate-900">API Status</div>
                        <div className="flex items-center gap-2 mt-1">
                          <div className={`h-2 w-2 rounded-full ${apiOk ? 'bg-emerald-500 animate-pulse' : apiOk === false ? 'bg-red-500' : 'bg-slate-400'}`} />
                          <span className="text-sm text-slate-600">
                            {pinging ? 'Checking...' : apiOk ? 'All systems operational' : apiOk === false ? 'Service disruption detected' : 'Checking status...'}
                          </span>
                        </div>
                      </div>
                    </div>
                    <div className="flex gap-2">
                      <button
                        onClick={pingApi}
                        disabled={pinging}
                        className="px-4 py-2 bg-slate-100 text-slate-700 hover:bg-slate-200 rounded-lg text-sm font-medium transition-colors disabled:opacity-50 flex items-center gap-2"
                      >
                        <ArrowPathIcon className={`h-4 w-4 ${pinging ? 'animate-spin' : ''}`} />
                        Check Status
                      </button>
                      <button
                        onClick={() => fetchData(true)}
                        disabled={refreshing}
                        className="px-4 py-2 bg-gradient-to-r from-blue-600 to-blue-700 text-white hover:from-blue-700 hover:to-blue-800 rounded-lg text-sm font-medium transition-colors disabled:opacity-50 flex items-center gap-2"
                      >
                        <ArrowPathIcon className={`h-4 w-4 ${refreshing ? 'animate-spin' : ''}`} />
                        Refresh Data
                      </button>
                    </div>
                  </div>
                </div>
              </ModernCard>
              
              <div className="mt-4 text-center">
                <p className="text-sm text-slate-500">
                  ¬© {new Date().getFullYear()} DocAnalyzer. All rights reserved.
                </p>
              </div>
            </div>
          </main>
        </div>
      </div>
    </div>
  );
}